.Home {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
}

.wall {
  width: 100%;
  height: 70vh;
  background-image: url(),
    radial-gradient(
      rgba(192, 173, 112, 0.2),
      rgba(92, 92, 204, 0.5),
      rgba(71, 71, 122, 0.8)
    );
}

.tree {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
}

.star {
  font-size: 30px;
  margin-left: -20px;
  margin-bottom: -10px;
  animation: spin 4s linear infinite alternate;
  z-index: 3;
}

.globe {
  position: absolute;
  font-size: 15px;
  z-index: 3;
  animation: shine 3s 1s infinite alternate-reverse;
}

.branches {
  animation: dancing_tree 0.5s infinite alternate;
  z-index: 2;
  margin-bottom: 120px;
}

.branch {
  border-right: 2px solid rgb(112, 52, 52);
  width: 45px;
  height: 1px;
  background: rgb(26, 44, 26);
}

.branch_bg {
  position: absolute;
  height: 5px;
  width: 40px;
  background-image: linear-gradient(
    to left,
    rgba(118, 158, 118, 0.7) 20%,
    rgba(38, 128, 51, 0.7) 30%,
    rgba(25, 58, 25, 0.5)
  );
  border-radius: 4px;
  z-index: 1;
}

.trunk {
  background: linear-gradient(
    to left,
    rgb(158, 134, 118) 20%,
    rgb(128, 59, 38) 30%,
    rgb(58, 33, 25)
  );
  height: 50px;
  width: 12px;
  border-radius: 10px 2px 5px 4px;
  transform: translateX(-3px);
}

.gift {
  position: absolute;
  top: 70%;
}

.floor {
  width: 100%;
  height: 30vh;
  background: linear-gradient(
    to top,
    rgb(119, 95, 81),
    rgb(58, 33, 25),
    rgb(153, 97, 64),
    rgb(58, 33, 25),
    rgb(119, 95, 81),
    rgb(58, 33, 25),
    rgb(128, 59, 38),
    rgb(58, 33, 25)
  );
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes shine {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

@keyframes dancing_tree {
  0% {
    transform: rotate(-3deg);
  }
  50% {
    transform: rotate(-2deg);
  }
  100% {
    transform: rotate(0);
  }
}
